.redux-container.templaza-framework-options{
  .tzfrm-ui-dialog{
    &.ui-widget-content{
        border: none;
        background: none;
        .ui-dialog-titlebar{
          -webkit-border-radius: 0;
          -moz-border-radius: 0;
          border-radius: 0;
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: 1px solid #dfdfdf;
        }
        + .ui-widget-overlay{
          opacity: 0.7;
          background: #000;
        }
        //.ui-icon{
        //  display: none;
        //}
        .ui-icon-closethick{
          display: none;
        }
        .ui-dialog-titlebar-close{
          border: none;
          font-size: 0;
        }
    }
    .button.ui-state-default{
        background-color: $tzbackground !important;
        border-color: $tzhover !important;
        background-image: none !important;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        text-shadow: none;
        &.button-primary{
          background-color: $tzhover !important;
          //border-color: $tzhover !important;
        }
        &:hover{
          color: $tzbackground;
          background-color: $tzhover !important;
        }
    }

    .redux-group-tab{
        display: block;
    }
  }
}

