  .ui-dialog-titlebar{
    position: absolute;
    width: 100%;
    height: 37px;
    max-width: 80vw;
    top: 10vh;
    left: 10vw;
    box-sizing: border-box;
    z-index: 2;
    background: #fcfcfc;
    border-bottom: 1px solid #dfdfdf;
    font-size: 18px;
    font-weight: 600;
    line-height: 2;
    padding: 0 36px 0 16px;

    //.tzfrm-ui-dialog &{
    //  z-index: 3;
    //}
  }
  .ui-dialog-content{
    height: 100% !important;
    max-height: 80vh !important;
    position: absolute;
    top: 10vh;
    left: 10vw;
    background: #fff;
    box-sizing: border-box;
    width: 100% !important;
    max-width: 80vw;
    z-index: 1;
    padding: 50px 16px 16px;
    overflow: auto;
  }
  &.ui-dialog-buttons{
    .ui-dialog-content{
      padding-bottom: 70px;
    }
  }
  .ui-dialog-buttonpane{
    position: absolute;
    bottom: 10vh;
    left: 10vw;
    z-index: 2;
    width: 100%;
    box-sizing: border-box;
    max-width: 80vw;
    background: #fcfcfc;
    border-top: 1px solid #dfdfdf;
    padding: 16px;
  }
  .ui-button.ui-dialog-titlebar-close{
    background: 0 0;
    border: none;
    box-shadow: none;
    color: #666;
    cursor: pointer;
    display: block;
    padding: 0;
    position: absolute;
    top: 0;
    right: 0;
    width: 36px;
    height: 36px;
    text-align: center;
    border-radius: 0;
    //.ui-icon {
    //  display: block;
    //  text-indent: -99999px;
    //  overflow: hidden;
    //  background-repeat: no-repeat;
    //}
    &:before{
      content: "\f158";
      font: normal 20px/1 dashicons;
      vertical-align: top;
      speak: none;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      line-height: 1.8;
      width: 36px;
      height: 36px;
    }
    &:focus {
      box-shadow: 0 0 0 1px #5b9dd9, 0 0 2px 1px rgba(30,140,190,.8);
      outline: 2px solid transparent;
      outline-offset: -2px;
    }
  }
  .ui-dialog-titlebar-close .ui-button-text {
    display: none;
  }
